<template>
  <v-app class="mt-16">
    <v-container class="mb-16">
      <v-row>
        <v-col cols="6">
          <v-card class="elevation-0">
            <h2>Lotes a subastar</h2>
          </v-card>
        </v-col>
        <v-col cols="5">
          <v-card class="elevation-0 d-flex justify-end">
            <v-btn
              class="elevation-5"
              @click="nuevoLote()"
              outlined
              color="green"
              v-if="$store.state.subasta.state !== 2"
              >Nuevo lote</v-btn
            >
          </v-card>
        </v-col>
        <v-col cols="1">
          <v-card class="elevation-0 d-flex justify-end">
            <v-btn
              class="elevation-5"
              @click="crearPDF()"
              outlined
              color="green"
            >
              <v-icon>mdi-printer</v-icon>
            </v-btn>
          </v-card>
        </v-col>
      </v-row>

      <v-row>
        <v-col cols="4" v-for="(l, i) in lotes" :key="i">
          
          <v-card class="mx-auto" max-width="300">
            <v-row no-gutters>
              <v-btn @click="recibido(l)">Recibido</v-btn>
            <v-spacer></v-spacer>
            <v-icon @click="reiniciarSubasta(l)">mdi-reload</v-icon>
            </v-row>
            
            <v-card-title>
              Subasta No. {{ l.sale.salenumber }}
              <v-spacer></v-spacer>
              <template v-if="l.state == '0' || l.state == '1'">
                <v-icon
                  class="mr-2"
                  color="green"
                  @click="activarDesactivar(l)"
                  v-if="l.state == '0'"
                  >mdi-eye</v-icon
                >
                <v-icon
                  class="mr-2"
                  color="red"
                  @click="activarDesactivar(l)"
                  v-else
                  >mdi-eye-off</v-icon
                >
              </template>
            </v-card-title>
            <v-card-text>
              <v-row align="center" class="mx-0">
                <div class="grey--text ms-4">Origen {{ l.origin }}</div>
              </v-row>
              <div class="my-4 text-subtitle-1">
                <span>Cantidad de cabezas {{ l.quantity }}</span>
              </div>
              <div my-4 text-subtitle-1>
                <span> Clase de ganado {{ l.classcattle }}</span>
              </div>
              <div my-4 text-subtitle-1>
                <span> Raza{{ l.breed.description }}</span>
              </div>
            </v-card-text>
            <v-divider class="mx-4"></v-divider>
            <v-card-title
              >Lote No. {{ l.lot }}
              <v-spacer></v-spacer>
              <template>
                <span class="green--text" text v-if="l.state == '0'"
                  >Inactivo</span
                >
                <span class="green--text" text v-if="l.state == '1'"
                  >Activo</span
                >
                <span class="orange--text" text v-if="l.state == '3'"
                  >Subasta en proceso</span
                >
                <span class="red--text" text v-if="l.state == '2'"
                  >Subastado</span
                >
              </template>
            </v-card-title>
            <v-card-actions>
              <template>
                <v-btn
                  color="green"
                  outlined
                  @click="subastar(l)"
                  v-if="l.state == '1' || l.state == '3'"
                >
                  Subastar
                </v-btn>
                <v-btn
                  color="green"
                  outlined
                  @click="rematar(l)"
                  v-if="l.state == '1' || l.state == '3'"
                >
                  Rematar
                </v-btn>
                <v-icon
                  class="mr-2"
                  color="red"
                  @click="reporte(l)"
                  v-if="l.state == '2'"
                  >mdi-file-pdf-box</v-icon
                >
              </template>

              <v-spacer></v-spacer>
              <v-icon
                class="mr-2"
                color="green"
                @click="editar(l)"
                v-if="l.state == '1' || l.state == '3'"
                >mdi-pencil
              </v-icon>
              <v-icon
                class="mr-2"
                color="green"
                @click="imprimir(l)"
                v-if="l.state == '2'"
                >mdi-printer</v-icon
              >
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
    </v-container>

    <!--  dialogo para insertar nuevo lote -->

    <v-dialog v-model="dialog" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>{{ titulo }}</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="10">
                <v-autocomplete
                  label="Proveedor..."
                  :items="holders"
                  v-model="proveedor"
                  item-value="value"
                >
                </v-autocomplete>
              </v-col>
              <v-col cols="12" sm="2" align-self="center">
                <v-btn outlined color="green" @click="abrirHolder()">
                  <v-icon>mdi-account-plus</v-icon>
                </v-btn>
              </v-col>
              <v-col cols="12" sm="6" v-if="bd === false">
                <v-text-field
                  label="Subasta"
                  v-model="subasta"
                  disabled
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field label="Origen" v-model="origen"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Cantidad"
                  v-model="cantidad"
                  type="number"
                  value="0"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Clase de ganado"
                  v-model="clase"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Peso"
                  v-model="peso"
                  type="number"
                  value="0"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Crias Hembra"
                  v-model="criasH"
                  type="number"
                  value="0"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6">
                <v-text-field
                  label="Crias Macho"
                  v-model="criasM"
                  type="number"
                  value="0"
                ></v-text-field>
              </v-col>

              <v-col cols="12" sm="6">
                <v-text-field label="Guia ICA" v-model="ica"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-textarea
                  label="Observaciones"
                  v-model="observaciones"
                ></v-textarea>
              </v-col>
              <v-col cols="12" sm="10">
                <v-autocomplete
                  label="Razas..."
                  :items="razas"
                  v-model="raza"
                  item-value="value"
                ></v-autocomplete>
              </v-col>
              <v-col cols="2" align-self="center">
                <v-btn outlined color="green" @click="abrirRaza()">
                  <v-img
                    src="../assets/icons/cow.png"
                    height="40px"
                    width="40px"
                  ></v-img>
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="elevation-3"
            outlined
            color="red"
            @click="cerrarDialog()"
          >
            Cerrar
          </v-btn>
          <v-btn
            class="elevation-3"
            outlined
            color="green"
            @click="accionLote()"
          >
            {{ etiqueta }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Cuadro de dialogo para insertar raza -->

    <v-dialog v-model="dialog2" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog2()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Guardar Raza</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  label="Descripcion"
                  type="text"
                  v-model="descripcion"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn outlined color="red" @click="cerrarDialog2()"> Cerrar </v-btn>
          <v-btn outlined color="green" @click="agregarRaza()"> Guardar </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Cuadro de dialogo para insertar holder -->

    <v-dialog v-model="dialog3" persistent max-width="600px">
      <v-toolbar dark color="green">
        <v-btn icon dark @click="cerrarDialog3()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>{{ titulo }}</v-toolbar-title>
      </v-toolbar>
      <v-card>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Documento"
                  type="number"
                  v-model="documento"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Telefono"
                  v-model="telefono"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre Completo"
                  v-model="nombre"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6" v-if="bd != true">
                <v-text-field label="Correo" v-model="correo"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Direccion"
                  v-model="direccion"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="Ciudad" v-model="ciudad"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field label="Banco" v-model="banco1"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 1"
                  v-model="numCuenta1"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field label="Banco" v-model="banco2"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 2"
                  v-model="numCuenta2"
                ></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field label="Banco" v-model="banco3"></v-text-field>
              </v-col>
              <v-col cols="12" sm="6" md="6">
                <v-text-field
                  label="Numero de cuenta 3"
                  v-model="numCuenta3"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Nombre completo de referencia"
                  v-model="referido"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                  label="Telefono de referencia"
                  v-model="telReferido"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn outlined color="red" @click="cerrarDialog3()"> Cerrar </v-btn>
          <v-btn outlined color="green" @click="agregarHolder()">
            Guardar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";
import jsPDF from "jspdf";
import "jspdf-autotable";
export default {
  name: "LotCattle",
  data() {
    return {
      dialog: false,
      dialog2: false,
      dialog3: false,
      bd: false,
      titulo: "Guardar Usuario",
      etiqueta: "Gurdar",
      subasta: localStorage.getItem("numSubasta"),
      //idSubasta: this.$store.state.subasta._id,
      idSubasta: localStorage.getItem("subasta"),
      idLote: "",
      proveedor: "",
      origen: "",
      cantidad: 0,
      clase: "",
      peso: 0,
      criasM: 0,
      criasH: 0,
      raza: "",
      ica: "",
      lotes: [],
      lotesPuja: [],
      holders: [],
      nombre: "",
      documento: "",
      telefono: "",
      correo: "",
      direccion: "",
      ciudad: "",
      referido: "",
      telReferido: "",
      banco1: "",
      banco2: "",
      banco3: "",
      numCuenta1: "",
      numCuenta2: "",
      numCuenta3: "",
      observaciones: "",
      razas: [],
      descripcion: "",
      subastas: [],
      conec: this.$store.state.conectado,
    };
  },

  sockets: {
    connect() {
      this.isConnected = true;
      console.log("conectado");
    },
    actualizarestado(subasta){
      this.lotes=subasta
      console.log(subasta);
    }
  },

  methods: {
    listarLotes() {
      console.log(this.idSubasta);
      //let header = { headers: { "x-token": this.$store.state.token } };
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios
        .get(`lotcattle/subasta/${this.idSubasta}`, header)
        .then((res) => {
          console.log(res);
          this.lotes = res.data.cattleLot;
          this.$store.state.subasta.state = res.data.cattleLot.sale.state;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    listarLotesPuja() {
      //let header = { headers: { "x-token": this.$store.state.token } };
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios
        .get(`lotcattle/puja/${this.idSubasta}`, header)
        .then((res) => {
          console.log(res);
          this.lotesPuja = res.data.cattleLot;
        })
        .catch((error) => {
          console.log(error);
        });
    },

    abrirHolder() {
      this.dialog3 = true;
    },

    agregarHolder() {
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      this.overlay = true;
      axios
        .post(
          "holder",
          {
            document: this.documento,
            name: this.nombre,
            email: this.correo,
            password: this.documento,
            phone: this.telefono,
            ciudad: this.ciudad,
            direccion: this.direccion,
            banco1: this.banco1,
            banco1numero: this.numCuenta1,
            banco2: this.banco2,
            banco2numero: this.numCuenta2,
            banco3: this.banco3,
            banco3numero: this.numCuenta3,
            referidopor: this.referido,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.overlay = false;
          this.listarProveedores();
          this.limpiarCajas3();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    listarProveedores() {
      //let header = { headers: { "x-token": this.$store.state.token } };
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios
        .get("holder", header)
        .then((response) => {
          console.log(response);
          response.data.holder.forEach((h) => {
            this.holders.push({
              text: " documento: " + h.document + " - " + h.name,
              value: h._id,
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    abrirRaza() {
      this.dialog2 = true;
    },

    agregarRaza() {
      this.overlay = true;
      //let header = { headers: { "x-token": this.$store.state.token } };
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios
        .post(
          "breed",
          {
            description: this.descripcion,
          },
          header
        )
        .then((response) => {
          console.log(response);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Registro exitoso",
            showConfirmButton: false,
            timer: 2000,
          });
          this.overlay = false;
          this.listarRazas();
          this.limpiarCajas2();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Error en registro</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
          this.overlay = false;
        });
    },

    listarRazas() {
      //let header = { headers: { "x-token": this.$store.state.token } };
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios
        .get("breed", header)
        .then((response) => {
          console.log(response);
          response.data.breed.forEach((h) => {
            this.razas.push({
              text: h.description,
              value: h._id,
            });
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },

    subastar(lote) {
      //this.$store.dispatch("setLote", lote);
      localStorage.setItem("lote", lote._id);
      localStorage.setItem("subasta", lote.sale._id);
      console.log(lote.state);
       if (lote.state == 1) {
        //let header = { headers: { "x-token": this.$store.state.token } };
        let header = { headers: { "x-token": localStorage.getItem("token") } };
        axios
          .put(
            `lotcattle/subastar/${lote._id}`,{},header
          )
          .then((res) => {
            console.log(res);
            Swal.fire({
              position: "bottom-end",
              icon: "success",
              title: "Subasta activa",
              showConfirmButton: false,
              timer: 2000,
            });
            this.listarLotes();
              window.open(
                "https://subasta-ganadera.herokuapp.com/#/saleLotCattle",
                "_blank"
              ); 
            //window.open("http://localhost:8080/#/saleLotCattle", "_blank");
          })
          .catch((err) => {
            console.log(err);
            Swal.fire({
              title: "<strong>Subasta rechazada</strong>",
              icon: "error",
              html: `<h4>${err.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
          });
      } else if (lote.state == 3) {
         window.open(
          "https://subasta-ganadera.herokuapp.com/#/saleLotCattle",
          "_blank"
        ); 
        //window.open("http://localhost:8080/#/saleLotCattle", "_blank");
      } 
    },

    rematar(lote) {
      console.log(lote.state);
      //this.$store.dispatch("setLote", lote);
      localStorage.setItem("lote", lote._id);
      localStorage.setItem("subasta", lote.sale._id);
      if (lote.state == 1) {
        //let header = { headers: { "x-token": this.$store.state.token } };
        let header = { headers: { "x-token": localStorage.getItem("token") } };
        axios
          .put(
            `lotcattle/rematar/${lote._id}`,{},header
          )
          .then((res) => {
            console.log(res);
            Swal.fire({
              position: "bottom-end",
              icon: "success",
              title: "Subasta activa",
              showConfirmButton: false,
              timer: 2000,
            });
            this.listarLotes();
             window.open(
                "https://subasta-ganadera.herokuapp.com/#/saleLotCattleRemate",
                "_blank"
              ); 
            //window.open("http://localhost:8080/#/saleLotCattleRemate", "_blank");
          })
          .catch((err) => {
            console.log(err);
            Swal.fire({
              title: "<strong>Subasta rechazada</strong>",
              icon: "error",
              html: `<h4>${err.response.data.errors[0].msg}</h4>`,
              showCloseButton: true,
            });
          });
      } else if (lote.state == 3) {
          window.open(
          "https://subasta-ganadera.herokuapp.com/#/saleLotCattleRemate",
          "_blank"
        ); 
        //window.open("http://localhost:8080/#/saleLotCattleRemate", "_blank");
      } 
    },

    editar(lote) {
      console.log(lote);
      this.dialog = true;
      this.bd = true;
      this.idLote = lote._id;
      this.etiqueta = "Editar";
      this.titulo = "Editar Lote";
      this.proveedor = lote.provider._id;
      this.origen = lote.origin;
      this.cantidad = lote.quantity;
      this.criasH = lote.calffemale;
      this.criasM = lote.calfmale;
      this.clase = lote.classcattle;
      this.ica = lote.ica;
      this.peso = lote.weight;
      this.raza=lote.breed._id
      this.observaciones = lote.observations;
    },

    nuevoLote() {
      this.dialog = true;
      this.bd = false;
      this.etiqueta = "Guardar";
      this.titulo = "Guardar Lote";
    },

    cerrarDialog() {
      this.dialog = false;
      this.limpiarCajas();
    },

    cerrarDialog2() {
      this.dialog2 = false;
      this.limpiarCajas2();
    },

    cerrarDialog3() {
      this.dialog3 = false;
      this.limpiarCajas2();
    },

    activarDesactivar(item) {
      console.log(item._id);
      Swal.fire({
        title: "Cambiar de estado",
        text: "Esta seguro que desea cambiar el estado del usuario?",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "green",
        cancelButtonColor: "red",
        confirmButtonText: "Si, cambiar estado",
      }).then((result) => {
        if (result.isConfirmed) {
          if (item.state === "1") {
            let header = {
              headers: { "x-token": localStorage.getItem("token") },
            };
            //let header = { headers: { "x-token": this.$store.state.token } };
            axios
              .put(`lotcattle/unactivate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarLotes();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          } else {
            //let header = { headers: { "x-token": this.$store.state.token } };
            let header = {
              headers: { "x-token": localStorage.getItem("token") },
            };
            axios
              .put(`lotcattle/activate/${item._id}`, {}, header)
              .then((response) => {
                console.log(response);
                this.listarLotes();
                Swal.fire("Ok", "El usuario ha cambiado de estado", "success");
              })
              .catch((error) => {
                console.log(error.response);
                Swal.fire("Error", "No se pudo cambiar el estado", "error");
              });
          }
        }
      });
    },

    accionLote() {
      if (this.bd === true) {
        console.log("estoy editando..");
        this.editarLote();
      } else {
        console.log("estoy agregando..");
        this.agregarLote();
      }
    },

    editarLote() {
      //let header = { headers: { "x-token": this.$store.state.token } };
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      axios
        .put(
          `lotcattle/${this.idLote}`,
          {
            provider: this.proveedor,
            origin: this.origen,
            quantity: this.cantidad,
            classcattle: this.clase,
            weight: this.peso,
            calfmale: this.criasH,
            calffemale: this.criasM,
            breed: this.raza,
            ica: this.ica,
            observations: this.observaciones,
          },
          header
        )
        .then((res) => {
          console.log(res);
          Swal.fire("Ok", "Actulizacion exitosa", "success");
          this.listarLotes();
          this.listarLotesPuja();
          this.limpiarCajas();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualizacin rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        });
    },

    agregarLote() {
      let header = { headers: { "x-token": localStorage.getItem("token") } };
      //let header = { headers: { "x-token": this.$store.state.token } };
      axios
        .post(
          "lotcattle",
          {
            sale: this.idSubasta,
            provider: this.proveedor,
            origin: this.origen,
            quantity: this.cantidad,
            classcattle: this.clase,
            weight: this.peso,
            calfmale: this.criasH,
            calffemale: this.criasM,
            breed: this.raza,
            ica: this.ica,
            observations: this.observaciones,
          },
          header
        )
        .then((res) => {
          console.log(res);
          Swal.fire({
            position: "bottom-end",
            icon: "success",
            title: "Actualizacin exitosa",
            showConfirmButton: false,
            timer: 2000,
          });
          this.listarLotes();
          this.listarLotesPuja();
          this.limpiarCajas();
        })
        .catch((error) => {
          console.log(error);
          Swal.fire({
            title: "<strong>Actualizacin rechazada</strong>",
            icon: "error",
            html: `<h4>${error.response.data.errors[0].msg}</h4>`,
            showCloseButton: true,
          });
        });
    },

    limpiarCajas() {
      this.proveedor = "";
      this.origen = "";
      this.cantidad = "";
      this.criasH = "";
      this.criasM = "";
      this.clase = "";
      this.raza = "";
      this.ica = "";
      this.peso = "";
      this.observaciones = "";
